const particleWorklet = URL.createObjectURL(
  new Blob(
    [
      `registerPaint(
        "particle",
        class {
          static get inputProperties() {
            return ["--particle-x", "--particle-y", "--particle-size", "--particle-color", "--particle-time", "--particle-num"];
          }
          paint(ctx, geom, properties) {
            const x = parseFloat(properties.get("--particle-x").toString());
            const y = parseFloat(properties.get("--particle-y").toString());
            const size = parseFloat(properties.get("--particle-size").toString());
            const color = properties.get("--particle-color").toString();
            const time = parseFloat(properties.get("--particle-time").toString());
            const num = parseFloat(properties.get("--particle-num").toString());

            ctx.globalAlpha = Math.max(1 - Math.sin(time * Math.PI / 2), 0);
            //  num 
            for (let i = 0; i < num; i++) {
              // 
              ctx.fillStyle = color ? color : \`hsl(\${Math.random() * 360}, 100%, 50%)\`;
              //  --particle-time 
              const particleX = x + time * 30 * Math.cos(i * Math.PI / 5); // 
              const particleY = y + time * 30 * Math.sin(i * Math.PI / 5) - time * time; // 
              // 
              ctx.beginPath();
              ctx.arc(particleX, particleY, size ?? 3, 0, 2 * Math.PI);
              ctx.fill();
            }
          }
        }
      );`,
    ],
    {
      type: "application/javascript",
    }
  )
);

export const createParticleWorklet = (
  { size } = {
    size: 3,
  }
) => {
  return URL.createObjectURL(
    new Blob(
      [
        `registerPaint(
          "particle",
          class {
            static get inputProperties() {
              return ["--particle-x", "--particle-y", "--particle-color", "--particle-time", "--particle-num"];
            }
            paint(ctx, geom, properties) {
              const x = parseFloat(properties.get("--particle-x").toString());
              const y = parseFloat(properties.get("--particle-y").toString());
              const color = properties.get("--particle-color").toString();
              const time = parseFloat(properties.get("--particle-time").toString());
              const num = parseFloat(properties.get("--particle-num").toString());
  
              ctx.globalAlpha = Math.max(1 - Math.sin(time * Math.PI / 2), 0);
              //  num 
              for (let i = 0; i < num; i++) {
                // 
                ctx.fillStyle = color ? color : \`hsl(\${Math.random() * 360}, 100%, 50%)\`;
                //  --particle-time 
                const particleX = x + time * 30 * Math.cos(i * Math.PI / 5); // 
                const particleY = y + time * 30 * Math.sin(i * Math.PI / 5) - time * time; // 
                // 
                ctx.beginPath();
                ctx.arc(particleX, particleY, ${size}, 0, 2 * Math.PI);
                ctx.fill();
              }
            }
          }
        );`,
      ],
      {
        type: "application/javascript",
      }
    )
  );
};

export default particleWorklet;
